@keyframes show-header {
  from {
    top: -150px;
  }
  to {
    opacity: 1;
    top: 0;
  }
}

@keyframes fadeIn {
  from {
    visibility: hidden;
    transform: translateY(-80px);
  }
  to { 
    visibility: visible;
    transform: translateY(0);
  }
}

@keyframes fadeOut {
  from {
    visibility: visible;
    transform: translateY(0);
  }
  to { 
    visibility: hidden;
    transform: translateY(-80px);
  }
}

.header-header {
  animation: fadeIn 1s ease 0s 1 normal forwards running;
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
}

.links-nav div {
  flex-grow: 1;
  text-align: center;
}

.navbar-brand {
  cursor: pointer;
}

.header-header nav div {
  justify-content: center;
  margin-left: 15px;
  margin-right: 15px;
}

